<template><markdown-wrapper><h1 id="buttongroup">ButtonGroup <a class="header-anchor" href="#buttongroup">&#128279;</a></h1>
<blockquote>
<p>Group a series of buttons together on a single line with the button group.</p>
</blockquote>
<p><strong>Note</strong>: When using tooltips or popovers on elements within a btn-group, make sure they're appended to body.</p>
<p><ins class="adsbygoogle"
     style="display:block; text-align:center;"
     data-ad-layout="in-article"
     data-ad-format="fluid"
     data-ad-client="ca-pub-4714899946256166"
     data-ad-slot="4603582855"></ins></p>
<h2 id="example">Example <a class="header-anchor" href="#example">&#128279;</a></h2>
<p>Wrap a series of <code v-pre>&lt;btn&gt;</code> in <code v-pre>&lt;btn-group&gt;</code>.</p>
<btn-group-example ref="btn-group-example"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">btn-group</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">btn</span>&gt;</span>Left<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">btn</span>&gt;</span>Middle<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">btn</span>&gt;</span>Right<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">btn-group</span>&gt;</span>
<span class="hljs-comment">&lt;!-- btn-group-example.vue --&gt;</span>
</code></pre>
<h2 id="button-toolbar">Button toolbar <a class="header-anchor" href="#button-toolbar">&#128279;</a></h2>
<p>Combine sets of <code v-pre>&lt;btn-group&gt;</code> into a <code v-pre>&lt;btn-toolbar&gt;</code> for more complex components.</p>
<btn-group-toolbar ref="btn-group-toolbar"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">btn-toolbar</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">btn-group</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">btn</span>&gt;</span>1<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">btn</span>&gt;</span>2<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">btn</span>&gt;</span>3<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">btn</span>&gt;</span>4<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">btn-group</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">btn-group</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">btn</span>&gt;</span>5<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">btn</span>&gt;</span>6<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">btn</span>&gt;</span>7<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">btn-group</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">btn-group</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">btn</span>&gt;</span>8<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">btn-group</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">btn-toolbar</span>&gt;</span>
<span class="hljs-comment">&lt;!-- btn-group-toolbar.vue --&gt;</span>
</code></pre>
<h2 id="sizing">Sizing <a class="header-anchor" href="#sizing">&#128279;</a></h2>
<p>Instead of applying button sizing classes to every button in a group, just add <code v-pre>size</code> to each <code v-pre>&lt;btn-group&gt;</code>, including when nesting multiple groups.</p>
<btn-group-sizes ref="btn-group-sizes"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>
