  <span class="hljs-tag">&lt;/<span class="hljs-name">dropdown</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">btn</span>&gt;</span>Right<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">btn-group</span>&gt;</span>
<span class="hljs-comment">&lt;!-- btn-group-nesting.vue --&gt;</span>
</code></pre>
<h2 id="vertical">Vertical <a class="header-anchor" href="#vertical">&#128279;</a></h2>
<p>Make a set of buttons appear vertically stacked rather than horizontally by adding <code v-pre>vertical</code>.</p>
<p><strong>Note</strong>: Split button dropdowns are not supported here.</p>
<btn-group-vertical ref="btn-group-vertical"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">btn-group</span> <span class="hljs-attr">vertical</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">btn</span>&gt;</span>Top<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
