<h1 id="api-reference">API Reference <a class="header-anchor" href="#api-reference">&#128279;</a></h1>
<h2 id="btngroup"><a href="https://github.com/uiv-lib/uiv/blob/0.x/src/components/button/BtnGroup.js">BtnGroup</a> <a class="header-anchor" href="#btngroup">&#128279;</a></h2>
<h3 id="props">Props <a class="header-anchor" href="#props">&#128279;</a></h3>
<div class="table-responsive"><table class="table table-bordered table-hover"><thead>
<tr>
<th>Name</th>
<th>Type</th>
<th>Default</th>
<th>Required</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td><code v-pre>size</code></td>
<td>String</td>
<td></td>
<td></td>
<td>Optional button sizes. Supported: <code v-pre>lg</code>, <code v-pre>sm</code>, <code v-pre>xs</code>.</td>
</tr>
<tr>
<td><code v-pre>vertical</code></td>
<td>Boolean</td>
<td>false</td>
<td></td>
<td>Apply vertical style.</td>
</tr>
<tr>
<td><code v-pre>justified</code></td>
<td>Boolean</td>
<td>false</td>
<td></td>
<td>Apply justified style.</td>
</tr>
</tbody>
</table></div><h3 id="slots">Slots <a class="header-anchor" href="#slots">&#128279;</a></h3>
<div class="table-responsive"><table class="table table-bordered table-hover"><thead>
